.rootCont {
  /* height: 90vh; */
  width: 70%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  /* padding-bottom: 10vh; */
  /* border: 2px solid black; */
  text-align: center;
}
.gitAntara{
 margin-top: 30px;
 margin-left: 95px;
 /* margin: auto; */
}
.rootCont h1 {
  color: rgb(103,50,29);

  margin: 0 auto;
}
.rootCont > p {
  font-size: 30px;
  font-weight: 400;
  color: rgb(208,135,104);
  /* margin: 10; */
}
.rootCont > p:nth-of-type(1) {
  font-size: 40px;
  font-weight: 400;
  color: black;
  /* margin: 50px; */
}
.skillsContFront {
  display: grid;
  grid-template-columns: repeat(6, 16.6%);
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 50px;
  /* margin-left: -50px; */
  /* height: 50vh; */
}
.skillsContBack {
  display: grid;
  grid-template-columns: repeat(3, 33.3%);
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 50px;
  /* height: 50vh; */
}
.toolsDiv {
  display: grid;
  grid-template-columns: repeat(4, 18.6%);
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  margin-bottom: 50px;
  /* height: 50vh; */
}
.skillCard {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100px;
  margin: auto;
}
.skillIcon {
  font-size: 40px;
  color: rgb(208,135,104);
}
.expSkillIcon {
  font-size: 31px;
  font-weight: 470;
  color: rgb(208,135,104);
}

.reactIcon:hover {
  color: rgb(94, 211, 243);
}
.reduxIcon:hover {
  color: rgb(118, 76, 183);
}
.jsIcon:hover {
  color: rgb(247, 223, 30);
}
.muiIcon:hover {
  color: rgb(0, 129, 203);
}
.htmlIcon:hover {
  color: rgb(233, 98, 40);
}
.cssIcon:hover {
  color: rgb(37, 75, 221);
}
.nodeIcon:hover {
  color: rgb(95, 151, 89);
}
.mongoIcon:hover {
  color: rgb(82, 167, 75);
}
.gitIcon:hover {
  color: rgb(232, 78, 49);
}
.expIcon {
  margin: 0;
}
.expIcon:hover {
  color: black;
}
@media only screen and (max-width: 1024px) {
  /* .rootCont {
    height: 140vh;
    padding-bottom: 5vh;
  } */
  .rootCont {
    width: 95%;
    margin-top: 4rem;
  }
  .skillsContFront {
    grid-template-columns: repeat(3, 33.3%);
    /* height: 120vh; */
  }
  .skillsContFront {
    width: 80%;
    display: grid;
    grid-template-columns: repeat(6, 16.6%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    margin: auto;
    /* border :1px solid black; */
    /* margin-left: -50px; */
    /* height: 50vh; */
  }
  .skillsContBack {
    width: 80%;
    display: grid;
    grid-template-columns: repeat(3, 33.3%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 50px;
    margin: auto;
    /* border :1px solid black; */
    /* height: 50vh; */
  }
  .toolsDiv {
    width: 80%;
    display: grid;
    grid-template-columns: repeat(4, 18.6%);
    justify-content: space-between;
    /* align-items: center; */
    margin: auto;
    border-radius: 10px;
    margin-bottom: 50px;
    /* border :1px solid black; */
    /* height: 50vh; */
  }
   
}

@media only screen and (max-width: 450px) {
  .toolsDiv {
    display: grid;
    grid-template-columns: repeat(3, 33.6%);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    margin-bottom: 10px;
    /* height: 50vh; */
  }
}