.rootCont {
  /* border: 2px solid black; */
  width: 60%;
  margin: auto;
  text-align: center;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  /* padding-bottom: 10vh; */
  /* border: 2px solid black; */

}
.rootCont h1 {
  color: rgb(103,50,29);
  width: 95px;
  margin: 0 auto;
}
.rootCont > p {
  font-size: 40px;
  font-weight: 400;
}
.mainCont {
  display: grid;
  grid-template-columns: 30% 60%;
  /* border: 2px solid black; */
  gap: 10%;
  color: rgb(103,50,29);
  font-size: 20px;
}
.mainCont > div {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  text-align: left !important;
}
.mainCont > div > h2 {
  margin: 0px;
}
/* 
.profilePicCont {
} */
.profilePic {
  text-align: right;
  width: 100%;
  margin: auto;
  /* border: 2px solid black; */
  background-color: rgb(194, 195, 201);
  border-radius: 10px;
}
@media only screen and (max-width: 1024px) {
  .rootCont {
    width: 50%;
    margin-top: 10rem;
    height: 100vh;
    /* border: 2px solid black; */
  }
  .mainCont {
    display: flex;
    flex-direction: column;
  }
  .mainCont > div {
    text-align: center !important;
  }
  .profilePicCont {
    padding: 30px;
    /* border: 2px solid black; */
  }
  .profilePic {
    width: 60%;
  }
}

@media only screen and (max-width: 450px) {
  .rootCont {
    width: 70%;
    margin-top: 8rem;
     /* border: 2px solid black; */
     padding-bottom: 10vh;
     height: 110vh;
  }
  .mainCont {
    display: flex;
    flex-direction: column;
  }
  .mainCont > div {
    text-align: center !important;
  }
  .profilePicCont {
    padding: 30px;
    /* border: 2px solid black; */
  }
  .profilePic {
    width: 60%;
  }
  .rootCont > p {
    font-size: 40px;
    font-weight: 400;
    margin-top: 100px;
  }
}